<template>
    <div v-if="isVisible" id="preview-modal">
        <div id="preview-content" style="overflow-y: hidden;">
            <div id="content1">
                <div class="header-wrap">
                    <span>
                        DOCUCMENT NO.
                    </span>
                </div>
                <header>
                    <h1>  </h1>
                    <p>PHYSICAL FITNESS TEST REPORT</p>
                    <img src="../assets/img/logo1.png">
                </header>

                

                <div class="content-wrap">
                    <p class="content-title"> </p>
                    <table id="info-table">
                        <tr>
                            <td id="title"></td>
                            <td id="info-element">
                                {{ props.userInfo. }}
                            </td>
                        </tr>
                        <tr>
                            <td id="title"></td>
                            <td id="info-element">{{ props.userInfo. }}</td>
                            <td id="title"></td>
                            <td id="info-element">{{ props.userInfo. }}</td>
                        </tr>
                        <tr>
                            <td id="title"></td>
                            <td id="info-element">{{ props.userInfo. }}</td>
                            <td id="title"></td>
                            <td id="info-element">{{ props.userInfo. }}</td>
                        </tr>
                        <tr>
                            <td id="title"></td>
                            <td id="info-element">{{ props.userInfo. }}</td>
                            <td id="title"></td>
                            <td id="info-element">{{ props.userInfo. }}</td>
                        </tr>
                    </table>
                </div>

                <div class="content-wrap">
                    <p class="content-title"> </p>
                    <div class="content">
                        <div>
                            <RadarChart :style="chartHeight"></RadarChart>
                        </div>
                        <div class="info-container">
                            <table id="info-table2">
                                <tr class="group">
                                    <td id="content-id"></td>
                                    <td></td>
                                    <td></td>
                                </tr>
                                <tr v-for="(item, key) in props.userData" :key="key">
                                    <td >{{ key }}</td>
                                    <td >{{ item. }}</td>
                                    <td >{{ item. }}</td>
                                </tr>
                            </table>
                        </div>
                    </div>
                </div>

                <div class="content-wrap ps-wrap">
                    <p class="content-title"> </p>
                    <div class="ps preview">
                        <p class="psTxt preview">
                            {{ props.userPs }}
                        </p>
                    </div>
                    <img class="footer-img" src="../assets/img/footer.jpg">
                </div>
                <span class="temp">
                    HANAPLUS
                </span>
            </div>
            <div class="sidebar" />
        </div>

        <div>
            <button class="close-btn" @click="closePreview"></button>
            <button class="confirm-btn" @click="downloadPDF">PDF </button>
        </div>


    </div>

</template>

<script setup>
import { defineProps, defineEmits, } from 'vue';
import RadarChart from './RadarChart.vue';
import html2pdf from 'html2pdf.js';

const props = defineProps({
    userInfo: {
        type: Object,
        required: true,
    },
    userData: {
        type: Object,
        required: true,
    },
    userPs: {
        type: String,
        required: true,
    },
    isVisible: {
        type: Boolean,
        default: false,
    },
});

const chartHeight = {
    height: '310px'
}

const emit = defineEmits(['update:isVisible']);

const closePreview = () => {
    emit('update:isVisible', false);
};

const downloadPDF = () => {
    const element = document.getElementById('preview-content');

    const opt = {
        margin: [0, 10, 0, 0],
        filename: 'physical_fitness_report.pdf',
        html2canvas: {
            scale: 3,
            useCORS: true,
            letterboxing: true,
        },
        jsPDF: {
            orientation: 'portrait',
            unit: 'mm',
            format: 'a4',
            hotfixes: ['px_scaling']
        },
    };
    html2pdf().from(element).set(opt).save();
};

</script>


<style scoped>
