<template>
    <div id="page-wrap">
        <div id="main-wrap">
            <span class="header-span">
                DOCUMENT NO.
            </span>
            <header>
                <h1>  </h1>
                <p>PHYSICAL FITNESS TEST REPORT</p>
            </header>
            <div class="img">
                <img src="../assets/img/logo1.png">
            </div>
            <div class="content-wrap">
                <p class="content-title"> </p>
                <table id="info-table">
                    <tr>
                        <td id="title"></td>
                        <td id="info-element">
                            {{ userInfo. }}
                        </td>
                    </tr>
                    <tr>
                        <td id="title"></td>
                        <td id="info-element">{{ userInfo. }}</td>
                        <td id="title"></td>
                        <td id="info-element">{{ userInfo. }}</td>
                    </tr>
                    <tr>
                        <td id="title"></td>
                        <td id="info-element">{{ userInfo. }}</td>
                        <td id="title"></td>
                        <td id="info-element">{{ userInfo. }}</td>
                    </tr>
                    <tr>
                        <td id="title"></td>
                        <td id="info-element">{{ userInfo. }}</td>
                        <td id="title"></td>
                        <td id="info-element">{{ userInfo. }}</td>
                    </tr>
                </table>
            </div>

            <div class="content-wrap">
                <p class="content-title"> </p>
                <div class="content">
                    <div>
                        <RadarChart :style="chartHeight"></RadarChart>
                    </div>
                    <div class="info-container">
                        <table id="info-table2">
                            <tr class="group">
                                <td id="content-id"></td>
                                <td></td>
                                <td></td>
                            </tr>
                            <tr v-for="(item, key) in userData" :key="key">
                                <td >{{ key }}</td>
                                <td >{{ item. }}</td>
                                <td >{{ item. }}</td>
                            </tr>
                        </table>
                    </div>
                </div>
            </div>

            <div class="content-wrap ps-wrap" style="margin-bottom: 0px;">
                <p class="content-title"> </p>
                <div class="ps">
                    <p class="psTxt">
                        {{ userPs }}
                    </p>
                </div>
                <img class="footer-img" src="../assets/img/footer.jpg">
            </div>

            <span class="temp">
                HANAPLUS
            </span>
            <div class="button-wrap">
                <button @click="showPreview">PDF </button>
            </div>
        </div>
        <div class="sidebar" :style="{ height : sidebarHeight}" />

    </div>


    <PreviewModal :userInfo="userInfo" :userData="userData" :userPs="userPs" :isVisible="showPreviewModal"
        @update:isVisible="showPreviewModal = $event" />
</template>

<script setup>
import RadarChart from '@/components/RadarChart.vue';
import { useStore } from '@/store/vueStore';
import { onMounted, ref, watch } from 'vue';
import { useRouter } from 'vue-router';

import PreviewModal from '@/components/PreviewModal.vue';

const chartStore = useStore();
const router = useRouter();
const sidebarHeight = ref('auto')


const showPreviewModal = ref(false)
var userInfo = ref([]);
var userData = ref();
var userPs = ref('')

const chartHeight = {
    height: '400px'
}

const updateSidebarHeight = () => {
    const mainWrap = document.getElementById('main-wrap')
    
    if (mainWrap) {
        sidebarHeight.value = `${mainWrap.offsetHeight}`
    }
}

const showPreview = () => {
    showPreviewModal.value = true;
    document.body.style.overflow = 'hidden'
}

watch(showPreviewModal, (isModalOpen) => {
    if (!isModalOpen) {
        document.body.style.overflow = ''
    }
})

onMounted(() => {

    if (!chartStore.chartData) {

        alert(' ')
        router.push('/')
    }

    if (chartStore.chartData) {

        const data = chartStore.chartData.reduce((acc, [key, val], idx) => {
            if (idx < 7) {

                acc.userInfo[key] = val;
            } else if (key !== "") {

                const reportKey = key.split('(')[0];
                const reportType = key.includes('') ? '' : '';

                if (!acc.userData[reportKey]) {
                    acc.userData[reportKey] = {
                        : null,
                        : null
                    }
                }

                acc.userData[reportKey][reportType] = val;
            } else if (key === '') {
                acc.userPs = val;
            }

            return acc;
        }, { userInfo: {}, userData: {}, userPs: '' })

        userInfo.value = data.userInfo;
        userData.value = data.userData;
        userPs.value = data.userPs;
    }

    updateSidebarHeight();
    window.addEventListener('resize', updateSidebarHeight)

})



</script>

<style>
.psTxt {
    margin-left: 10px;
}




#title {
    font-weight: 900 !important;
}
</style>