<template>
    <div class="chart-container" :style="chartStyle">
        <canvas id="radarChart" ref="radarChartRef"></canvas>
    </div>
  
    <!--
    <div>
        <canvas id="gradeChart" ref="gradeChartRef"></canvas>
    </div>
  -->
</template>

<script setup>
import { ref, onMounted, watch, defineProps, computed } from 'vue';
import { Chart, registerables } from 'chart.js';
import { useStore } from '@/store/vueStore';
import { useRouter } from 'vue-router';

Chart.register(...registerables);

const chartStore = useStore();
const router = useRouter();

const radarChartInstance = ref(null);
const radarChartRef = ref(null);

const props = defineProps({
  style : {
    type: Object,
    default: () => ({})
  }
})

const chartStyle = computed(() => props.style)


const transformData = (chartData) => {
  const labels = [];
  const datasets = [
    {
      label: '',
      data: [],
      backgroundColor: 'rgba(255, 165, 0, 0.2)',
      borderColor: 'rgba(255, 165, 0, 1)',
      borderWidth: 1,
    }
  ];

  chartData.forEach(([key, val]) => {
    if (key.includes('()')) { 
      labels.push(key);
      datasets[0].data.push(val);
    }
  });

  return { labels, datasets };
}

onMounted(() => {
  if (!chartStore.chartData) {
    router.push('/');
  }

  if (radarChartRef.value) {
    const initialData = transformData(chartStore.chartData);

    radarChartInstance.value = new Chart(radarChartRef.value, {
      type: 'radar',
      data: initialData,
      options: {
        responsive: true,
        scales: {
          r: {
            beginAtZero: true,
            ticks : {
              font:{
                size: 18, 
                weight: 'bold', 
                family: 'Arial', 
                color: 'rgba(255, 165, 0, 1)', 
              },
              stepSize :1,
            }
          },
        },
        plugins: {
          legend : {
            display:false,
          }
        }

      },


    });
  }
});

watch(
  () => chartStore.chartData,
  (newData) => {
    if (radarChartInstance.value) {
      const updatedData = transformData(newData);

      radarChartInstance.value.data.labels = updatedData.labels;
      radarChartInstance.value.data.datasets = updatedData.datasets;

      radarChartInstance.value.update();
    }
  },
  { deep: true }
);
</script>